const Base = require('../base.js');
//添加文章
module.exports = class extends Base {
    async indexAction() {
        var date_s = {}
        date_s.title = this.ctx.post('biaoti')
        date_s.text = this.ctx.post('sdf_drt')
        date_s.addtime = new Date().getTime()
        date_s.userId = this.cookie('user_id')
        date_s.liulan = 0
        const model = this.model('article');
        const result = await model.thenAdd(date_s, {
            title:  date_s.title
        });
        var jhde = {}
        if (result.type == "exist") { //已存在
            jhde.code = -1
            jhde.msg = "文章已存在，请勿重复添加"
        } else {
            jhde.code = 0
            jhde.msg = "文章添加成功"
        }
        return this.fail(jhde); //2输出json 推荐

    }

};
